.paginator {
    margin: 10px;
    display: flex;
   
}
.pageNumber {
    padding: 2px;
    cursor: pointer;
    font-family: preconnect;
    padding: 7px;
}

.pageNumber.selectedPage {
    font-weight: bold;
    font-size: 15px;
    border-color: black;
}

.paginatorBtn {
    color: #000000;
    background-color: rgb(229,229,229);
    padding: 0px 8px 4px 8px;
}  

